.popup {
    width: 100%;
    height: 100vh;
    position: fixed;
    top: 0;
    right: 0;
    
    margin: 20px 0 !important;
}

#popup-article {
    z-index: 22;
}

.popup::before {
    content: "";
    box-sizing: border-box;
    width: 100%;
    background-color: #fff;
    position: fixed;
    left: 0;
    top: 50%;
    will-change: height, top;
    animation: open-animation .6s cubic-bezier(0.83, 0.04, 0, 1.16) .65s both;
    
}

.popup::after {
    content: "";
    width: 0;
    height: 2px;
    background-color: #f0f0f0;
    will-change: width, opacity;
    animation: line-animation .6s cubic-bezier(0.83, 0.04, 0, 1.16) both;
    position: absolute;
    top: 50%;
    left: 0;
    margin-top: -1px;
}

@keyframes line-animation {
    0% {
        width: 0;
        opacity: 1;
    }
    99% {
        width: 100%;
        opacity: 1;
    }
    100% {
        width: 100%;
        opacity: 0;
    }
}

@keyframes open-animation {
    0% {
        height: 0;
        top: 50%;
    }
    100% {
        height: 100vh;
        top: 0;
    }
}

.popup__block {
    height: calc(100vh - 40px);
    padding: 5% 15%;
    box-sizing: border-box;
    position: relative;
    margin: auto;
    overflow: auto;
    animation: fade .5s ease-out 1.3s both;
    
}

.popup__block p {
    font-size: 18px;
    color: #4a4a4a;
    line-height: 1.25;
    font-weight: 300;
}

@keyframes fade {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

#span {
    background-color: #432E30 !important
}

.toggle.open #span {
    background-color: transparent !important;
}

.popup__title {
    font-size: 1.5rem;
    margin: 0 0 1em;
    color: #000;
    font-weight: bold;
}

.popup__close {
    width: 2rem;
    height: 2rem;
    text-indent: -9999px;
    position: fixed;
    top: 20px;
    right: 20px;
    background-repeat: no-repeat;
    background-position: center center;
    background-size: contain;
    background-image: url(data:image/svg+xml;base64,);
}